body[scheme="bright_light"] {
    --header-r: 255;
    --header-g: 255;
    --header-b: 255;
    --header-text: 0;
}
body[scheme="space_gray"] {
    --header-r: 25;
    --header-g: 25;
    --header-b: 26;
    --header-text: 255;
}
#artistsHeader .PanelHeader__content {
    -webkit-user-select: none;
    user-select: none;
    cursor: default;
    color: rgba(var(--header-text), var(--header-text), var(--header-text), 0);
}
#artistsHeader .PanelHeader__in {
    background: rgba(var(--header-r), var(--header-g), var(--header-b), 0);
}